.bullet {
  position: relative;

  &:before {
    content: '';
    display: inline-block;
    width: 10px;
    height: 10px;
    border-radius: 50%;
    background-color: #fff;
    border: 3px solid $color-grey-400;
    margin-right: $space-xxs;
  }

  &.-success,
  &.-green {
    &:before {
      border-color: $color-green-600;
    }
  }

  &.-danger,
  &.-red {
    &:before {
      border-color: $color-red-600;
    }
  }

  &.-warning,
  &.-yellow {
    &:before {
      border-color: $color-yellow-600;
    }
  }

  &.-info,
  &.-blue {
    &:before {
      border-color: $color-blue-600;
    }
  }

  &.-grey {
    &:before {
      border-color: $color-grey-500;
    }
  }
}
