.stack-list {
  display: flex;
  flex-direction: column;
  padding: $space-m 0;
  margin: 0;

  &.-minimal {
    padding: 0;

    .stack {
      padding: 0 $space-xs $space-s $space-xs;
      border: none;

      &:not(:last-of-type) {
        border-bottom: 1px solid $color-grey-200;
        padding-bottom: $space-s;
      }

      & + .stack {
        margin-top: $space-s;
      }
    }
  }
}

.stack {
  position: relative;
  list-style: none;
  border: 1px solid $color-grey-300;
  padding: $space-s 24px;

  &:not(:last-of-type) {
    border-bottom: none;
  }

  &:first-of-type {
    border-radius: 6px 6px 0 0;
  }

  &:last-of-type {
    border-radius: 0 0 6px 6px;
  }

  &:only-child {
    border-radius: 6px;
  }

  p {
    font-size: $size-text-s;
    color: $color-grey-500;
    white-space: pre-line;
    line-height: 1.25;
  }
}

.stack-actions {
  position: absolute;
  display: flex;
  right: 12px;
  top: 12px;
}

.stack-action {
  font-size: $size-text-xs !important;
}

.stack-title {
  font-size: $size-text-base;
  margin: 0;
  line-height: 2;
  font-weight: 500;
}

.stack-footer {
  padding-top: $space-s;
}

.stack-label {
  font-size: $size-text-s;
  color: $color-grey-500;
}

.stack-label-icon {
  font-size: $size-text-xs;
  color: $color-grey-700;

  & + .stack-label {
    margin-left: 3px;
  }
}
