.barchart {
  display: inline-flex;
  position: relative;
  width: 174px;
  height: 6px;
  background-color: $color-grey-200;
  border-radius: $border-radius-base;
  overflow: hidden;

  + .barchart-legend {
    margin-left: $space-s;
  }
}

.barchart-partial {
  background-color: $color-grey-500;

  &[data-bar-percent="60"] {
    flex-basis: 60%;
  }

  &[data-bar-percent="10"] {
    flex-basis: 6%;
  }

  &.-grey {
    background-color: $color-grey-500
  }

  &.-blue {
    background-color: $color-blue-600;
  }
}

.barchart-legend {
  display: inline-flex;
}

.barchart-legend-item {
  font-size: $size-text-s;
  color: $color-grey-500;

  & + .barchart-legend-item {
    margin-left: $space-s;
  }
}

.barchart-legend-icon {
  font-size: $size-text-xxs;
  margin-right: 4px;
  color: $color-grey-400;

  &.-blue {
    color: $color-blue-600;
  }

  &.-grey {
    color: $color-grey-600;
  }
}
